.filter{
    display: flex;
    flex-direction: row;
    padding: 20px 50px;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    margin: 20px 0px;
}
.search-option{
    display: flex;
    list-style-type: none;
    color: #8c8c8c;
}
.search-option-item{
    display: flex;
    flex-direction: column;
    font-weight: bold;
    padding: 5px 15px;
}
.search{
    font-weight: bold;
    background-color: #695fe2;
    padding: 10px 20px;
    color: #fff;
    border: 1px solid #695fe2;
    border-radius: 5px;
    cursor: pointer;
}
.search-input{
    border: 0;
    font-weight: bold;
    width: 140px;
}

.property-cards{
    align-self: center;
    display: grid;
    width: 900px;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    justify-content: space-around;
    align-items: center;
}
