.navbar{
    background-color: #f8f7fd;
    height: 60px;
    color: #01021e;
    border-bottom: 1px solid #dddce0;
}
.container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 10px 150px;
}
.link-container{
    display: flex;
    width: 600px;
    justify-content: space-around;
    align-items: center;
}
.logo-link{
    text-decoration: none;
    color: #01021e;
}
.links{
    display: flex;
    list-style-type: none;
}
.fav-link{
    text-decoration: none;
    color: #01021e;
}
.fav-link:hover{
    color: #695fe2;
    cursor: pointer;
    background-color: #d6ceed;
    border-radius: 5px;
}
.item{
    margin: 0 20px;
    font-weight: bold;
    padding: 10px;
}
.item:hover{
    color: #695fe2;
    cursor: pointer;
    background-color: #d6ceed;
    border-radius: 5px;
}
.logo{
    font-size: x-large;
    font-weight: bold;
}
.auth-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.auth-container a{
    text-decoration: none;
}
.auth-link{
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: bold;
}
.login{
    color: #695fe2;
    border: 1px solid #695fe2;
    margin-right: 5px;
}
.sign-up{
    color: #fff;
    background-color: #695fe2;
    margin-left: 5px;
    border: 1px solid;
    border-color: #695fe2;
}