.card{
    background-color: #fff;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.house-image{
    width:100%;
    height: 150px;
}
.card-content{
    padding: 10px;
}
.rent{
    display: flex;
    justify-content: space-between;
    align-items: center;

    span{
        padding: 5px;
        border: 1px solid gray;
        border-radius: 50%;
        cursor: pointer;
    }
}
